.movie-container {
  background-color: rgb(24, 30, 109);
  display : flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.movie-box {
  width: 250px;
  height: 400px;
  margin: 10px;
  text-align: center;
  background-color: rgb(51, 58, 163);
  padding: 10px;
  box-sizing: border-box;
  position: relative;
  transition: filter 0.3s ease;
}

.movie-poster {
  width: 100%;
  height : 85%;
  position: relative;
  z-index: 1;
}

.movie-title {
  text-align: left;
  margin-top: 10px;
  color: white;
  font-size: 13px;
}

.movie-grade {
  text-align: right;
  color: white;
  font-size: 12px;
}

.movie-overview {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.hovered .movie-overview {
  opacity: 1;
}